import SQDateRangeSlider from "../../daterange-slider";
import { BaseModule } from "./module";

SQDateRangeSlider.defaults({
  currentView: "month",
  viewInterval: {
    year: 1,
    month: 1,
    week: 1,
    day: 1,
    hour: 1,
    minute: 15,
    seconds: 60,
  },
  focusInterval: {
    year: 1,
    month: 3,
    week: 4,
    day: 7,
    hour: 4,
    minute: 60,
    seconds: 240,
  },
  visibleRange: {
    year: 3,
    month: 3,
    week: 32,
    day: 7,
    hour: 12,
    minutes: 24,
    seconds: 500,
  },
});

class Range extends BaseModule {
  constructor(slider) {
    super(slider);
    this.classes = this.utils.classes.generate({
      range: {
        prefix: "sq-range",
        column: "column",
        outerContainer: "outer-container",
        container: "container",
        group: "group",
        groupWrapper: "group-wrapper",
        groupHeader: "group-header",
        itemBlock: "item-block",
        itemText: "item-text",
        intervalPoint: "interval-point",
        leftIcon: "left",
        rightIcon: "right",
      },
    });

    this.parentGroupNames = {
      month: "year",
    };

    this.groupNames = {
      year: "none",
      month: "year",
      week: "month",
      day: "month",
      hour: "day",
      minute: "hour",
      seconds: "minute",
    };
    this.childNames = {
      none: "year",
      year: "month",
      month: "week",
      month: "day",
      day: "hour",
      hour: "minute",
      minute: "seconds",
    };

    this.intervalTypes = {
      minute: "minute",
    };

    /** Groups. */
    this.groups = {
      month: "YYYY",
      week: "MMMM",
      day: "MMMM YYYY",
      hour: "D MMM, YYYY",
      minute: "D MMM, YYYY",
      seconds: "mm",
    };
    /** Items. */
    this.itemFormat = {
      year: "YYYY",
      week: "D/M",
      month: "MMM",
      day: "D",
      hour: "hA",
      minute: "hh:mmA",
      seconds: "mm:ss",
    };
    const range = this.getVisibleRange(
      this.slider.datetime.today().date(),
      this.slider.config.currentView
    );
    this._currentPage = {
      from: range.start,
      to: range.end,
    };
  }

  _attachEvents() {
    var that = this;
    this.slider.$on(
      this.$el,
      "click",
      `.${this.classes.range.leftIcon}`,
      function () {
        that.prev();
      }
    );
    this.slider.$on(
      this.$el,
      "click",
      `.${this.classes.range.rightIcon}`,
      function () {
        that.next();
      }
    );
  }

  getIntervalInSeconds(currentView) {
    switch (currentView) {
      case "seconds":
        return this.slider.config.viewInterval[currentView] * 1000;
      case "minute":
        return this.slider.config.viewInterval[currentView] * 60 * 1000;
      case "hour":
        return this.slider.config.viewInterval[currentView] * 60 * 60 * 1000;
      case "day":
        return (
          this.slider.config.viewInterval[currentView] * 24 * 60 * 60 * 1000
        );
      case "week":
        return (
          this.slider.config.viewInterval[currentView] * 7 * 24 * 60 * 60 * 1000
        );
      case "month":
        return (
          this.slider.config.viewInterval[currentView] *
          30 *
          24 *
          60 *
          60 *
          1000
        );
    }
  }

  init() {
    const slider = this.slider;
    this.$el = this.slider.$tlcontainer;
    this.$el.addClass(this.classes.range.root);
    this.$el.append(`
    <div class="${this.classes.range.main}">
      <div class="${this.classes.range.column} sq-button ${
      this.classes.range.leftIcon
    }">
        ${slider.icon.get("left-arrow", { size: "medium" })}
      </div>
      <div class="${this.classes.range.column} ${
      this.classes.range.outerContainer
    }">
        <div class="${this.classes.range.groupWrapper}">

        </div>
        <div class="${this.classes.range.container}">

        </div>
      </div>
      <div class="${this.classes.range.column} sq-button ${
      this.classes.range.rightIcon
    }">
        ${slider.icon.get("right-arrow", { size: "medium" })}
      </div>
    </div>
    `);
    this.$main = this.$el.find(`.${this.classes.range.main}`);
    this.$outerContainer = this.$el.find(
      `.${this.classes.range.outerContainer}`
    );
    this.$container = this.$el.find(`.${this.classes.range.container}`);
    this.$groupWrapper = this.$el.find(`.${this.classes.range.groupWrapper}`);
    this.$left = this.$el.find(`.${this.classes.range.left}`);
    this.$right = this.$el.find(`.${this.classes.range.right}`);

    this._attachEvents();
    this.render();
  }

  set(options = {}) {
    let changeDetected = false;
    Object.keys(options).forEach((option) => {
      if (this.slider.config[option] !== options[option]) {
        changeDetected = true;
        this.slider.config[option] = options[option];
      }
    });
    changeDetected && this.render();
  }

  getGroup(interval) {
    return this.groups[interval];
  }

  getItemFormat(interval) {
    return this.itemFormat[interval];
  }

  getDateFromRange(range) {
    var value = range.value;
    var index = Math.floor(
      (value.width + value.left).toFixed(2) / this.currentItemWidth
    );
    var marginLeft = Math.floor(value.left.toFixed(2) / this.currentItemWidth);
    var remIndex =
      (value.width + value.left).toFixed(2) % this.currentItemWidth;
    var remLeft = value.left.toFixed(2) % this.currentItemWidth;
    const percentageL = (remLeft / this.currentItemWidth) * 100;
    const percentageR = (remIndex / this.currentItemWidth) * 100;
    if (percentageL < 15) {
      remLeft = 0;
    } else if (percentageL > 85) {
      remLeft = 0;
      marginLeft++;
    }
    if (percentageR > 85) {
      remIndex = 0;
      index++;
    } else if (percentageR < 10) {
      remIndex = 0;
    }
    var fromDate = this.currViewRange.items[marginLeft].date;
    var toDate;
    if (Math.floor(remIndex) === 0) {
      if (this.currViewRange.items.length === index) {
        toDate = this.slider.datetime
          .new(this.currViewRange.items[index - 1].date)
          .add(
            this.slider.config.viewInterval[this.slider.config.currentView] - 1,
            this.slider.config.currentView
          )
          .endOf(this.slider.config.currentView)
          .date();
      } else {
        toDate = this.slider.datetime
          .new(this.currViewRange.items[index].date)
          .add(-1, this.childNames[this.slider.config.currentView])
          .endOf(this.childNames[this.slider.config.currentView])
          .date();
      }
    } else {
      toDate = this.slider.datetime
        .new(this.currViewRange.items[index].date)
        .date();
    }
    var timeUnit = this.getIntervalInSeconds(this.slider.config.currentView);

    var startAdd = Math.floor(remLeft) / this.currentItemWidth;
    var endAdd = Math.floor(remIndex) / this.currentItemWidth;
    var timeSAddd = timeUnit * startAdd;
    var timeEAddd = timeUnit * endAdd;

    return {
      fromDate: new Date(fromDate.getTime() + timeSAddd),
      toDate: new Date(toDate.getTime() + timeEAddd),
    };
  }

  next() {
    const interval = this.groupNames[this.slider.config.currentView];
    const range = this.getVisibleRange(
      this._currentPage.from
        .add(this.slider.config.visibleRange[interval], interval)
        .date(),
      this.slider.config.currentView
    );
    this._currentPage.from = range.start;
    this._currentPage.to = range.end;
    this.render();
  }

  prev() {
    const interval = this.groupNames[this.slider.config.currentView];
    const range = this.getVisibleRange(
      this._currentPage.from
        .add(this.slider.config.visibleRange[interval] * -1, interval)
        .date(),
      this.slider.config.currentView
    );
    this._currentPage.from = range.start;
    this._currentPage.to = range.end;
    this.render();
  }

  render() {
    this.currViewRange = this.getRange(
      this._currentPage.from,
      this.slider.config.currentView
    );
    const htmlGroup = `
    <div class="${this.classes.range.groupWrapper}">
      ${Object.keys(this.currViewRange.groups)
        .map((groupKey) => {
          return this.createGroupWrapper(
            groupKey,
            this.currViewRange[groupKey]
          );
        })
        .join("")}
    </div>`;
    const htmlContent = `<div class="${this.classes.range.container} ${
      this.slider.config.currentView
    }">
      ${this.currViewRange.items
        .map((item) => {
          return this.createItem(item);
        })
        .join("")}
    </div>`;
    this.$groupWrapper.html(htmlGroup);
    this.$container.html(htmlContent);
    this.adjustGroups();
    this.events.emit("onRender", this, { ctx: this, el: this.$outerContainer });
  }

  getItemWidth() {
    var width = this.$(
      this.$outerContainer.find(`.${this.classes.range.itemBlock}`)[0]
    ).width();
    return width;
  }
  adjustGroups() {
    var width = this.getItemWidth();
    var accumulatedWidth = 0;
    Object.keys(this.currViewRange.groups).forEach((groupKey, idx) => {
      var totalWidth = this.currViewRange.groups[groupKey].totalDays * width;
      this.$(
        this.$outerContainer.find(`.${this.classes.range.group}`)[idx]
      ).css({
        left: accumulatedWidth,
        width: totalWidth,
      });
      accumulatedWidth += totalWidth;
    });
    this.currentItemWidth = width;
  }

  createGroupWrapper(groupKey, config) {
    return `<div class="${this.classes.range.group}">
      <div class="${this.classes.range.groupHeader}">${groupKey}</div>
    </div>`;
  }

  createItem(item) {
    return `<div class="${this.classes.range.itemBlock} ${
      item.intervalPoint ? `${this.classes.range.intervalPoint}` : ""
    }">
      <span class="${this.classes.range.itemText}">
        ${item.displayDate}
      </span>
    </div>`;
  }

  getVisibleRange(from, interval) {
    var start, end;
    switch (this.groupNames[interval]) {
      default:
        start = this.slider.datetime
          .new(from)
          .startOf(this.groupNames[interval]);
        end = this.slider.datetime
          .new(start)
          .add(
            this.slider.config.visibleRange[this.groupNames[interval]] - 1,
            this.groupNames[interval]
          )
          .endOf(this.groupNames[interval]);
    }
    return {
      start,
      end,
    };
  }

  getRange(from, interval) {
    const visbleRange = this.getVisibleRange(from, interval);
    var start = visbleRange.start;
    var end = visbleRange.end;
    var output = [];
    let counter = -1;
    const groups = {};
    while (start.date() < end.date()) {
      const fnCounter = this.intervalTypes[interval];
      if (fnCounter && start[fnCounter]) {
        counter = start[this.intervalTypes[interval]]();
      } else {
        counter++;
      }
      const group = start.format(this.getGroup(interval));
      if (!groups[group]) {
        groups[group] = {
          totalDays: 1,
        };
      } else {
        groups[group].totalDays++;
      }
      output.push({
        date: start.date(),
        group: group,
        intervalPoint:
          this.slider.config.focusInterval[interval] === 1 ||
          (this.slider.config.focusInterval[interval] !== 1 &&
            (counter + 1) % this.slider.config.focusInterval[interval] === 1),
        displayDate: start.format(this.getItemFormat(interval)),
      });
      start.add(this.slider.config.viewInterval[interval], interval);
    }
    return {
      groups,
      items: output,
    };
  }
}

SQDateRangeSlider.registerModule("range", Range);
export default Range;
