import { BaseModule } from "../core/module";
import SQDateRangeSlider from "../../daterange-slider";

SQDateRangeSlider.defaults({});

SQDateRangeSlider.ACTIONS = {
  prev: {
    icon: "left-arrow",
    FA5_NAME: "left-arrow",
    text: "Prev",
    execute: function () {
      this.slider.range.prev();
    },
  },
  next: {
    icon: "right-arrow",
    FA5_NAME: "right-arrow",
    text: "Next",
    execute: function () {
      this.slider.range.next();
    },
  },
  year: {
    icon: "text",
    template: "TEXT",
    FA5_NAME: "text",
    text: "Y",
    execute: function () {
      var slider = this.slider;
      slider.switchView("year");
    },
  },
  month: {
    icon: "text",
    template: "TEXT",
    FA5_NAME: "text",
    text: "M",
    execute: function () {
      var slider = this.slider;
      slider.switchView("month");
    },
  },
  week: {
    icon: "text",
    template: "TEXT",
    text: "W",
    execute: function () {
      var slider = this.slider;
      slider.switchView("week");
    },
  },
  day: {
    icon: "text",
    template: "TEXT",
    text: "D",
    execute: function () {
      var slider = this.slider;
      slider.switchView("day");
    },
  },
  hour: {
    icon: "text",
    template: "TEXT",
    text: "h",
    execute: function () {
      var slider = this.slider;
      slider.switchView("hour");
    },
  },
  minute: {
    icon: "text",
    template: "TEXT",
    text: "m",
    execute: function () {
      var slider = this.slider;
      slider.switchView("minute");
    },
  },
  zoomIn: {
    icon: "text",
    template: "TEXT",
    text: "+",
    execute: function () {
      var slider = this.slider;
      slider.zoomIn();
    },
  },
  zoomOut: {
    icon: "text",
    template: "TEXT",
    text: "-",
    execute: function () {
      var slider = this.slider;
      slider.zoomOut();
    },
  },
};

SQDateRangeSlider.createAction = function (name, options) {
  SQDateRangeSlider.ACTIONS[name] = options;
};

class Actions extends BaseModule {
  constructor(slider) {
    super(slider);
  }

  init() {}

  get(name) {
    return SQDateRangeSlider.ACTIONS[name];
  }

  exec(action, args) {
    return SQDateRangeSlider.ACTIONS[action].execute.call(this, args);
  }
}
SQDateRangeSlider.registerModule("actions", Actions);

export default Actions;
