import SQDateRangeSlider from "../../daterange-slider";
import { utils } from "sq-core/vanillajs";
const MomentDateTime = utils.MomentDateTime;
SQDateRangeSlider.defaults({
  format: "MM/DD/YYYY hh:mm A",
});

function DateTimeModule() {
  return {
    DateTime: MomentDateTime,
    new: (date) => {
      return new MomentDateTime(date);
    },
    today: () => {
      return new MomentDateTime().startOf("day");
    },
    now: () => {
      return new MomentDateTime();
    },
  };
}

SQDateRangeSlider.registerModule("datetime", DateTimeModule);

export default DateTimeModule;
