import { BaseModule } from "./module";
import SQDateRangeSlider from "../../daterange-slider";
const levelOrder = ["year", "month", "week", "day", "hour", "minute"];
SQDateRangeSlider.defaults({
  //default config goes here
  levels: ["day", "hour", "minute"],
});

class SwitchView extends BaseModule {
  constructor(slider) {
    super(slider);
    this.classes = this.utils.classes.generate({
      switch: {
        prefix: "sq-switch-view",
        button: "button",
        buttonText: "button-text",
      },
    });
  }

  init() {
    this.$el = this.slider.$switchView;
    this.render();
    this.bindEvents();
  }

  onAction(e, target) {
    const slider = this.slider;
    slider._internalEvents.emit("onViewChange", {
      value: this.$(target).data("item"),
    });
  }

  bindEvents() {
    this.slider.$on(
      this.$el,
      "click",
      `.${this.classes.switch.button}`,
      this.onAction,
      this
    );
  }

  getLevels(levels) {
    return levelOrder.filter((item) => {
      return levels.indexOf(item) > -1;
    });
  }

  render() {
    const html = `<div class="${this.classes.switch.root}">
      ${this.getLevels(this.slider.config.levels)
        .map((level) => {
          return `<button class="sq-button sq-button--primary ${this.classes.switch.button}" data-item="${level}">
        <span class="sq-button-text ${this.classes.switch.buttonText}">${level}</span>
      </button>`;
        })
        .join("")} </div>`;
    this.$el.html(html);
  }
}

// SQDateRangeSlider.registerModule("switchView", SwitchView);

export { SwitchView };
